import React, { useState, useMemo, useEffect } from 'react';
import { useParams, useLocation } from 'react-router-dom';
import { Table, Card, Container, Row, Col, Form } from 'react-bootstrap';
import { FaSort, FaDownload } from 'react-icons/fa';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import { Pagination } from 'antd';
import "./Userlist.css";
import { getDownstreamListAsmZsmReport, UseReportAll } from '../../../api/login/Login';
import Loader from '../../../common/loader/Loader';

function Mainwalltreportzsm() {
    const { userId } = useParams();
    const location = useLocation();

    const [reportData, setReportData] = useState([]);
    const [allReportData, setAllReportData] = useState([]);
    const [totalCount, setTotalCount] = useState(0);
    const [totalamount, setTotalAmount] = useState(0);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [sortConfig, setSortConfig] = useState({ key: null, direction: 'ascending' });
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage, setItemPerPage] = useState(10);

    const getCurrentDate = () => {
        const today = new Date();
        return today.toISOString().substr(0, 10);
    };

    const initialFilterState = {
        user_id: window.localStorage.getItem('userIdToken'),
        id: userId,
        count: itemsPerPage,
        page: 1,
        start_date: getCurrentDate(),
        end_date: getCurrentDate(),
        type: '',
        trans_type: '',
        order_id: '',
        txn_id: ''
    };

    const [filterValues, setFilterValues] = useState(initialFilterState);
    const [appliedFilters, setAppliedFilters] = useState(initialFilterState);

    const fetchReportData = async () => {
        try {
            setLoading(true);
            setError(null);

            const payload = {
                ...appliedFilters,
                page: currentPage,
                count: itemsPerPage
            };

            const response = await getDownstreamListAsmZsmReport(payload);

            if (response?.data?.data) {
                setReportData(Array.isArray(response.data.data.wallet) ? response.data.data.wallet : []);
                setTotalCount(response.data.data.total || 0);
                setTotalAmount(response.data.totalAmount || 0);
            } else {
                setReportData([]);
                setTotalCount(0);
                setTotalAmount(0);
                setError("No data available");
            }
        } catch (error) {
            console.error('Error fetching user report:', error);
            setError(error?.message || "Failed to fetch report data");
            setReportData([]);
            setTotalCount(0);
            setTotalAmount(0);
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchReportData();
    }, [userId, currentPage, itemsPerPage, appliedFilters]);

    const fetchAllReportData = async () => {
        try {
            setLoading(true);
            setError(null);
            const response = await UseReportAll({
                ...appliedFilters,
                page: 1,
                count: totalCount || 1000
            });

            if (response?.data?.data) {
                setAllReportData(Array.isArray(response.data.data) ? response.data.data : []);
            } else {
                setAllReportData([]);
            }
        } catch (error) {
            console.error('Error fetching all user report data:', error);
            setError(error?.message || "Failed to fetch complete report data");
            setAllReportData([]);
        } finally {
            setLoading(false);
        }
    };

    const handleFilterChange = (event) => {
        const { name, value } = event.target;
        setFilterValues(prevState => ({
            ...prevState,
            [name]: typeof value === 'string' ? value.trim() : value
        }));
    };

    const handleSearch = () => {
        setError(null);
        setAppliedFilters(filterValues);
        setCurrentPage(1);
    };

    const handleReset = () => {
        const resetState = {
            ...initialFilterState,
            start_date: "",
            end_date: ""
        };
        setError(null);
        setFilterValues(resetState);
        setAppliedFilters(resetState);
        setCurrentPage(1);
        setItemPerPage(10);
    };

    const requestSort = (key) => {
        let direction = 'ascending';
        if (sortConfig.key === key && sortConfig.direction === 'ascending') {
            direction = 'descending';
        }
        setSortConfig({ key, direction });
    };

    const sortedAndFilteredData = useMemo(() => {
        const dataToSort = Array.isArray(reportData) ? [...reportData] : [];

        if (sortConfig.key && dataToSort.length > 0) {
            return dataToSort.sort((a, b) => {
                const aVal = a[sortConfig.key] ?? '';
                const bVal = b[sortConfig.key] ?? '';

                if (aVal < bVal) return sortConfig.direction === 'ascending' ? -1 : 1;
                if (aVal > bVal) return sortConfig.direction === 'ascending' ? 1 : -1;
                return 0;
            });
        }
        return dataToSort;
    }, [reportData, sortConfig]);

    const handleExcelDownload = async (e) => {
        if (allReportData.length === 0) {
            e.preventDefault();
            await fetchAllReportData();
        }
    };

    if (loading) {
        return (
            <Container fluid className="px-4">
                <Loader />
            </Container>
        );
    }

    return (
        <Container fluid className="px-4 PageHeading"
            style={{ backgroundColor: "var(--bs-primary1)", color: "var(--bs-primary2)" }}
        >
            <h1 className="mt-4 mb-4" style={{ fontWeight: 700 }}>Transaction Report</h1>

            <Card className="mb-4 ur-card"
                style={{ backgroundColor: "var(--bs-primary1)", border: "1px solid var(--bs-primary2)" }}
            >
                <Card.Header className="ur-card-header"
                    style={{ backgroundColor: "var(--bs-primary3)", borderBottom: "1px solid var(--bs-primary2)" }}
                ></Card.Header>

                <Card.Body className="ur-card-body">
                    <Form>
                        <Row className="mb-3">
                            <Col md={4}>
                                <Form.Group controlId="startDate">
                                    <Form.Label style={{ color: "var(--bs-primary2)" }}>Start Date</Form.Label>
                                    <Form.Control
                                        type="date"
                                        name="start_date"
                                        value={filterValues.start_date}
                                        onChange={handleFilterChange}
                                        max={getCurrentDate()}
                                        style={{
                                            backgroundColor: "var(--bs-primary1)",
                                            color: "var(--bs-primary2)",
                                            border: "1px solid var(--bs-primary2)"
                                        }}
                                    />
                                </Form.Group>
                            </Col>

                            <Col md={4}>
                                <Form.Group controlId="endDate">
                                    <Form.Label style={{ color: "var(--bs-primary2)" }}>End Date</Form.Label>
                                    <Form.Control
                                        type="date"
                                        name="end_date"
                                        value={filterValues.end_date}
                                        onChange={handleFilterChange}
                                        max={getCurrentDate()}
                                        style={{
                                            backgroundColor: "var(--bs-primary1)",
                                            color: "var(--bs-primary2)",
                                            border: "1px solid var(--bs-primary2)"
                                        }}
                                    />
                                </Form.Group>
                            </Col>

                            <Col md={4}>
                                <Form.Group controlId="type">
                                    <Form.Label style={{ color: "var(--bs-primary2)" }}>Type</Form.Label>
                                    <Form.Control
                                        as="select"
                                        name="type"
                                        value={filterValues.type}
                                        onChange={handleFilterChange}
                                        style={{
                                            backgroundColor: "var(--bs-primary1)",
                                            color: "var(--bs-primary2)",
                                            border: "1px solid var(--bs-primary2)",
                                            borderRadius: "10px",
                                            fontWeight: 600,
                                            height: "38px",
                                            cursor: "pointer"
                                        }}
                                    >
                                        <option value="" disabled>Select Type</option>
                                        <option value="all">All</option>
                                        <option value="credit">Credit</option>
                                        <option value="debit">Debit</option>
                                    </Form.Control>

                                </Form.Group>
                            </Col>
                        </Row>

                        <Row className="mb-3">
                            <Col md={4}>
                                <Form.Group controlId="txn_id">
                                    <Form.Label style={{ color: "var(--bs-primary2)" }}>Txn Id</Form.Label>
                                    <Form.Control
                                        type="text"
                                        name="txn_id"
                                        value={filterValues.txn_id}
                                        onChange={handleFilterChange}
                                        style={{
                                            backgroundColor: "var(--bs-primary1)",
                                            color: "var(--bs-primary2)",
                                            border: "1px solid var(--bs-primary2)"
                                        }}
                                    />
                                </Form.Group>
                            </Col>
                        </Row>

                        <Row className="mb-3">
                            <div className="form-group col-md-6 col-lg-4 d-flex gap-2">
                                {/* <label>&nbsp;</label> */}
                                <button
                                    type="button"
                                    className="btn"
                                    onClick={handleSearch}
                                    style={{
                                        backgroundColor: "var(--bs-primary3)",
                                        color: "var(--bs-primary2)",
                                        border: "1px solid var(--bs-primary2)",
                                        padding: "6px 14px",
                                        borderRadius: "10px",
                                        fontWeight: 600
                                    }}
                                >
                                    Search
                                </button>

                                <button
                                    type="button"
                                    className="btn"
                                    onClick={handleReset}
                                    style={{
                                        backgroundColor: "transparent",
                                        color: "var(--bs-primary2)",
                                        border: "1px solid var(--bs-primary2)",
                                        padding: "6px 14px",
                                        borderRadius: "10px",
                                        fontWeight: 600
                                    }}
                                >
                                    Reset
                                </button>
                            </div>
                        </Row>
                    </Form>
                </Card.Body>
            </Card>

            <Card className="mb-4 ur-card"
                style={{ backgroundColor: "var(--bs-primary1)", border: "1px solid var(--bs-primary2)" }}
            >
                <Card.Header className="ur-card-header"
                    style={{ backgroundColor: "var(--bs-primary3)", color: "var(--bs-primary2)", borderBottom: "1px solid var(--bs-primary2)" }}
                >
                    <Row className="align-items-center">
                        {/* <Col md={12} className="text-end">
                            {sortedAndFilteredData.length > 0 && (
                                <ReactHTMLTableToExcel
                                    id="test-table-xls-button"
                                    className="er-download-btn"
                                    table="table-to-xls"
                                    filename="user_report"
                                    sheet="user_report"
                                    buttonText={<><FaDownload /> Download Excel</>}
                                    style={{ cursor: "pointer" }}
                                    onClick={handleExcelDownload}
                                />
                            )}
                        </Col> */}
                    </Row>
                </Card.Header>

                <Card.Body className="ur-card-body">
                    <Table
                        responsive
                        striped
                        bordered
                        hover
                        id="table-to-xls"
                        className="ur-table"
                        style={{
                            backgroundColor: "var(--bs-primary1)",
                            color: "var(--bs-primary2)",
                            border: "1px solid var(--bs-primary2)"
                        }}
                    >
                        <thead
                            style={{
                                backgroundColor: "var(--bs-primary3)",
                                color: "var(--bs-primary2)",
                                textAlign: "center",
                                fontWeight: 600
                            }}
                        >
                            <tr>
                                <th onClick={() => requestSort('_id')} className="ur-th" style={{ minWidth: '100px' }}>
                                    S.No <FaSort />
                                </th>
                                <th onClick={() => requestSort('createdAt')} className="ur-th" style={{ minWidth: '180px' }}>
                                    Date <FaSort />
                                </th>
                                <th onClick={() => requestSort('order_id')} className="ur-th">Order ID <FaSort /></th>
                                <th onClick={() => requestSort('txn_id')} className="ur-th">Transaction Id <FaSort /></th>
                                <th onClick={() => requestSort('o_bal')} className="ur-th" style={{ minWidth: '180px' }}>
                                    Opening Balance <FaSort />
                                </th>
                                <th onClick={() => requestSort('amount')} className="ur-th" style={{ minWidth: '180px' }}>
                                    Amount <FaSort />
                                </th>
                                <th onClick={() => requestSort('c_bal')} className="ur-th" style={{ minWidth: '180px' }}>
                                    Closing Balance <FaSort />
                                </th>
                                <th onClick={() => requestSort('trans_type')} className="ur-th">Transaction Type <FaSort /></th>
                                <th onClick={() => requestSort('type')} className="ur-th">Type <FaSort /></th>
                                <th onClick={() => requestSort('message')} className="ur-th">Message <FaSort /></th>
                            </tr>
                        </thead>

                        <tbody>
                            {sortedAndFilteredData.length > 0 ? (
                                sortedAndFilteredData.map((item, index) => (
                                    <tr key={item._id || index} className="ur-tr" style={{ textAlign: "center" }}>
                                        <td>{(currentPage - 1) * itemsPerPage + index + 1}</td>
                                        <td>{item?.createdAt || ''}</td>
                                        <td>{item?.order_id || ''}</td>
                                        <td>{item?.txn_id || ''}</td>
                                        <td>{item?.o_bal ?? 0}</td>
                                        <td>{item?.amount ?? ''}</td>
                                        <td>{item?.c_bal ?? 0}</td>
                                        <td>{item?.trans_type || ''}</td>
                                        <td>{item?.type || ''}</td>
                                        <td>{item?.message || ''}</td>
                                    </tr>
                                ))
                            ) : (
                                <tr>
                                    <td colSpan="10" className="text-center">
                                        {error || "No Data Found"}
                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </Table>

                    {sortedAndFilteredData.length > 0 && (
                        <div
                            className="d-flex justify-content-between align-items-center mt-3"
                            style={{
                                backgroundColor: "var(--bs-primary1)",
                                color: "var(--bs-primary2)",
                                borderTop: "1px solid var(--bs-primary2)",
                                padding: "10px"
                            }}
                        >
                            <p className="ur-total-count mb-0">Total Count: {totalCount}</p>
                            <p className="ur-total-count mb-0">Total Amount: {totalamount}</p>

                            <Pagination
                                current={currentPage}
                                total={totalCount}
                                pageSize={itemsPerPage}
                                showSizeChanger
                                onChange={(page, perPageitem) => {
                                    setCurrentPage(page);
                                    setItemPerPage(perPageitem);
                                }}
                            />
                        </div>
                    )}
                </Card.Body>
            </Card>
        </Container>
    );
}

export default Mainwalltreportzsm;
